







import Vue from 'vue';

export default Vue.extend({
  props: {
    blockType: {
      type: Number,
      required: true,
    },
    clicked: {
      type: Function,
      required: true,
    },
    drag: {
      type: Function,
      required: true,
    },
    row: {
      type: Number,
      required: true,
    },
    col: {
      type: Number,
      required: true,
    },
  },
  methods: {
    click(event: MouseEvent) {
      this.clicked(event.button, this.row, this.col);
    },
    dragged(event: MouseEvent) {
      this.drag(event.button, this.row, this.col);
    },
  },
  computed: {
    classTypes() {
      return this.blockType === 0 ? '' : 'selected';
    },
  },
});
